footer{
margin-top:auto; 
}

.App {
    padding: 0;
    margin: 0;
    display:flex; 
    flex-direction:column; 
    height:100%;
}

.logo {
    max-width: 150px;
    width: 100%;
    display: block;   
}

.navbar,
footer {
    background-color:#234465;
}

a.nav-link {
    color: #fff;    
    text-transform: uppercase;
    font-size: 13px;
    font-weight: 600;
}

a.nav-link:not(.primary-btn) {
    opacity: 0.5;
}

a.nav-link:hover {
    opacity: 1;
}

.primary-btn {
    background-color: #ffa000;
    padding: 6px 15px;
    border-radius: 20px;
    opacity: 1;
    text-transform: capitalize !important;
    transition: all .2s ease;
}

.primary-btn:hover {
    background-color: #fff;
    color: #234465 !important;
}

.form-heading:first-of-type {
    border-right: 2px solid #29394B;
}

h3 {
    color: #bbb;
    font-size: 22px;
}

h3:hover {
    cursor: pointer;
    color: #44B1FA;
}

h3.active-login-form,
h3.active-register-form {
    color: #29394B;
}

.btn-primary {
    background-color: #44A9F8;
    border: 0;
    width: 50%;
}

.btn-primary:hover {
    background-color: #6DCCF3;
}